import css from './images.scss'

function Image(
  src,
  alt,
  size
 ) {
  return () => (
    <img
      className={css.logo}
      src={BASE_LINK + src + '-' + size + '.png'}
      style={{ width: size / 2, height: size / 2, margin: size / 8 }}
      alt={alt}
    />
  )
}

const Images = {
  Logo: Image('/static/favicon', 'Conscripter Logo', 96)
}

export default Images